import { Component , OnInit , ViewChild } from '@angular/core';
import {AlertController, ModalController ,  IonRouterOutlet} from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import {  Router , NavigationExtras } from '@angular/router';
import { AuthenticationService } from '../../services/Authentication.service';
import { FormGroup, FormControl } from '@angular/forms';
import { MyModalcardPage } from '../../../DESARROLLO/modals/my-modalcard/my-modalcard.page';
import { ReplaySubject} from 'rxjs';
import { IonInfiniteScroll } from '@ionic/angular';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-producto-compra2',
  templateUrl: './producto-compra2.page.html',
  styleUrls: ['./producto-compra2.page.scss'],
})
export class ProductoCompra2Page implements OnInit {
  @ViewChild(IonInfiniteScroll, { static : false }) infiniteScroll: IonInfiniteScroll;
rangeForm: FormGroup;
searchQuery: string;
showAgeFilter = false;
showmenu = false;
llave:any;
categoria:any;
  items: any  = [] ;
  variables: any = [];
  userinfo: any = [];
  viewType = 'grid';
  moneda:any;
  tipovista:any;

  tituloxx = '';
  efecto = 0;
  seguro = '';
  msg = '';
  apifoto : String;
  cartItemCount: BehaviorSubject<number>;
  sienvio:any = false;
  
  lat: number;
    lng: number;
    address: string;
  
  
  itemsList: Array<any>;
  public currentSegment: string = "Todos";
  searchSubject: ReplaySubject<any> = new ReplaySubject<any>(1);

constructor(   private routerOutlet: IonRouterOutlet , private router: Router, private authenticationService: AuthenticationService,
   public alertController: AlertController,     public translateService: TranslateService    ,    private modalController: ModalController
   ) { this.apifoto = this.authenticationService.getapifoto();
 }
//producto-detalle1
  ngOnInit() {
    this.variables = this.router.getCurrentNavigation().extras.state.variables;
    this.llave = this.router.getCurrentNavigation().extras.state.llave0;
    this.categoria = this.router.getCurrentNavigation().extras.state.categoria;
    this.tipovista = this.router.getCurrentNavigation().extras.state.tipovista;
    this.sienvio = this.router.getCurrentNavigation().extras.state.sienvio;
    this.moneda = this.router.getCurrentNavigation().extras.state.moneda;
    this.userinfo = this.authenticationService.getcopiauser();
    this.tituloxx =   this.router.getCurrentNavigation().extras.state.titulo ;
    this.searchQuery = '';
    this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value  });
    this.translateService.get('MSGM').subscribe((value) => {   this.msg = value  });
    this.cartItemCount = this.authenticationService.getCartItemCount();
    this.pideregistros();
  }

 
  pideregistros0() {
   this.pideregistros();
  }
  pideregistros() {
       this.itemsList = [];
       this.efecto = 0 ;
       this.authenticationService.getReg('todosproductos/Activo/' + this.userinfo[0].idempresa._id + '/' + this.llave +'°'+this.categoria, 'productos', 0).then((data) => {
  
       this.efecto=1;
       this.items =  data;
       this.itemsList = this.items;
 }, (err) => {
   this.authenticationService.presentAlert(err.error , this.msg , '');
 });
  }
  searchList() {
 
    const query = (this.searchQuery && this.searchQuery !== null) ? this.searchQuery : '';
    this.itemsList = this.filterList(this.items, query);
  }
 filterList(list, query): Array<any> {
   return list.filter(item => item.nombre.toLowerCase().includes(query.toLowerCase()));
  }
  loadData(event) {
    setTimeout(() => {
      event.target.complete();
      if ( this.items.length === 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }


  masprod(item, event: any) {
    console.log(event.currentTarget.innerText);
    this.authenticationService.addProduct({idproveedor: this.llave ,id: item._id,  name: item.nombre + ' ' + item.xunidad + ' ' + item.unidad,  price: item.precio,  amount: 1,rate:''});
}



async openCart() {
  const modal = await this.modalController.create({
    component: MyModalcardPage,
    animated: true,
    componentProps:  {sienvio:this.sienvio ,moneda:this.moneda, nombre:  this.userinfo[0].nombre , direccion: this.userinfo[0].direccion,  telefono: this.userinfo[0].telefono, correo: this.userinfo[0].correo   },
   
    presentingElement: await this.modalController.getTop() , cssClass: 'modal-fullscreen'
  });
  modal.onWillDismiss().then((data) => {
  });
  return await modal.present();
}


damenu(i, item) {


  const navigationExtras: NavigationExtras = {
    state: {
      variables: this.variables,
      user: 'this.username',
      llave: item._id,
      sienvio:this.sienvio,
      llave0: this.llave,
      tipovista: this.tipovista,
      moneda: this.moneda,
      data: item,
      titulo: 'Producto ' + item.nombre
    }
  };
  this.router.navigate(['producto-detalle1'], navigationExtras);

}


}
